import {React, useState, useEffect} from 'react';
import { useNavigate, useLocation } from 'react-router-dom';
import API from '../../../../Services';
import PageWrapper from '../../../../layout/PageWrapper/PageWrapper';
import Page from '../../../../layout/Page/Page';
import Button from '../../../../components/bootstrap/Button';
import { cirurgicalFlow } from '../../../../menu';
import Header from '../../../../components/Header';
import  {DropdownItem, DropdownMenu, DropdownToggle} from '../../../../components/bootstrap/Dropdown';
import Dropdown from '../../../../components/bootstrap/Dropdown';
import Card, {CardBody} from '../../../../components/bootstrap/Card';
import Icon from '../../../../components/icon/Icon';
import "./EncaminharSala.css"


const EncaminharSala = () => {

const navigate = useNavigate()
const location = useLocation()


const [Salas,	setSalas] = useState([])
/**
 * Método responsável em buscar todas as salas do Centro Cirurgico.
 */
const buscaSalas = async() => {
	try {			
		const buscaSala = await API.get('/retornaSalasDisponiveisCirurgia');
		const response = buscaSala.data
		setSalas(response)
	} catch (error) {
		console.log(error)
	}
};



const redirecionarPagina = (codigoSala)=>{
	const session = JSON.parse(localStorage.getItem('CAPP-local'))
					
	navigate('/inducao/AssinaturaSala', {
		state: {
			CODIGO_LOCALIDADE: session.CODIGO_LOCALIDADE,
			CODIGO_SUB_LOCALIDADE: session.CODIGO_SUBLOCALIDADE,
			DADOS: location.state.DADOS,
			DATA_INICIO_JEJUM: location.state.DATA_INICIO_JEJUM,
			CODIGO_PROX_ETAPA: location.state.CODIGO_PROX_ETAPA,
			CODIGO_SALA: codigoSala
		}

	  });
}


useEffect(() => {
	buscaSalas()
	console.log(location.state)
}, [location]);


const alterarLocalidade = () => {

	localStorage.removeItem('CAPP-local');
	navigate('/')

};

const sessionQ = JSON.parse(localStorage.getItem('CAPP-local'))
const localidadeHeader = `Indução L${sessionQ.SUBLOCALIDADE.split(' ')[1]}`;


	return (
		<PageWrapper title={cirurgicalFlow.localChoice.text}>
			<Header titulo={localidadeHeader} localidade='Encaminhamento Sala Cirúrgica'>
				<Dropdown >
					<DropdownToggle>
						<Button
							color="light"
							icon="menu"
							isLight
						>
						<p> </p>
						</Button>
					</DropdownToggle>
					<DropdownMenu>
						<DropdownItem onClick={() => alterarLocalidade()}>
							Alterar Local
						</DropdownItem>
					</DropdownMenu>
				</Dropdown>
			</Header>
			<Page>
				<br /><br />
				<h2>Informe a <strong>sala Cirúrgica:</strong></h2><br /><br />
				<div className='listagem-salas'>
				{
					 Salas.map((i) => {
						return (
						   	<Card className="bloco" key={i.CD_SAL_CIR} onClick={()=> redirecionarPagina(i.CD_SAL_CIR)}>
								<CardBody>
									{i.DS_SAL_CIR}	
								</CardBody>
					   		</Card>
				   		);
			   		})
				}
			</div>

			<div className='btn-back'>
					<Icon   onClick={() => window.history.go(-1)}
							icon='reply' 
							color= '#fff' 
							size='6x' 
							forceFamily= 'material' 
					/>
				</div>


			</Page>
		</PageWrapper>
	);
};

export default EncaminharSala;
